<template>
	<div class="row" style="padding: 0 10px;">
		<div class="col-sm-12 col-lg-8">
			<cai-pin></cai-pin>
		</div>
		<div class="col-sm-12 col-lg-4">
			<h3 class="text-muted my-5">菜单</h3>
			<table class="table">
				<thead class="table table-default">
					<tr>
						<th>品种</th>
						<th>删除</th>
					</tr>
				</thead>
				<tbody v-for="(item,index) in getmenuitem">
					<tr>
						<td>{{item.name}}</td>
						<td><button class="btn btn-sm btn-outline-success" @click="dedlete(item,index)">x</button></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
	import caipin from './admin_newcp.vue'
	export default {
		data() {
			return {
				name: 'qilonglong',
//				getmenuitem: []
			}
		},
		components: {
			'cai-pin': caipin
		},
		computed:{
			getmenuitem:{
				get(){
					return this.$store.state.menuItems
				},
				set(){
					
				}
			}
		},
		created() {
			fetch("https://wd4709732241nqopkc.wilddogio.com/menu.json")
				.then(res => {
					return res.json()
				}).then(data => {
					console.log(data)
					let menuArray = []
					for(let key in data) {

						data[key].index = key;
						//					console.log(data[key].id)
						menuArray.push(data[key])

//						this.getmenuitem = menuArray
//						把menuArray传到vuex同步,新的数据
						this.$store.commit('setmentitems',menuArray)
						console.log(this.getmenuitem)
					}
				})
		},
		methods: {
			dedlete(item, index) {
				console.log(item, index)
				fetch("https://wd4709732241nqopkc.wilddogio.com/menu/" + item.index + "/.json", {
						method: 'DELETE',
						headers: {
							'Content-type': 'application/json'
						}
					})
					.then(res => {
						return res.json()
					}).then(data => {
						console.log(data)
						
						this.$store.commit('remove_menushuju',item)
						
//						this.$router.push({name:'menuLink'})
						
					})
			}
		}
		//		进入路由之前
		//		beforeRouteEnter:(to,from,next) => {   
		////			alert('hello'+"............."+this.name)  访问不到name
		////			next()
		//			next(vm =>{
		//				alert('hello'+"............."+vm.name) 
		//			})
		//		},
		//		离开路由触发
		//		beforeRouteLeave (to,from,next){
		//			if(confirm('确认离开吗') == true){
		//				console.log(to)
		//				next()
		//			}else{
		//				next(false)
		//			}
		//		}
	}
</script>

<style>

</style>


