<template>
	<form>
		<h3 class="text-muted my-2">添加新的菜品</h3>
		<div class="d">
			<label for="Mima">菜名：</label>
			<input type="text" class="form-control" id="Mima" v-model="newcaipin.caipin">
		</div>
		<div class="">
			<label for="Mima2">品种：</label>
			<input type="text" class="form-control" id="Mima2" v-model="newcaipin.pinzhong">
		</div>
		<p class="d"><strong>选项1</strong></p>
		<div class="d">
			<label for="Mima3">尺寸：</label>
			<input type="text" class="form-control" id="Mima3" v-model="newcaipin.chicun">
		</div>
		<div class="d">
			<label for="Mima4">价格：</label>
			<input type="text" class="form-control" id="Mima4" v-model="newcaipin.jiage">
		</div>
		<p class="d"><strong>选项2</strong></p>
		<div class="d">
			<label for="Mima5">尺寸：</label>
			<input type="text" class="form-control" id="Mima5" v-model="newcaipin.chicun2">
		</div>
		<div class="d">
			<label for="Mima6">价格：</label>
			<input type="text" class="form-control" id="Mima6" v-model="newcaipin.jiage2">
		</div>

		<div class="d">
			<button class="btn btn-block btn-success" @click.prevent="fn">确定</button>
		</div>
	</form>
</template>

<script>
	
	export default {
		data() {
			return {
				newcaipin: []
			}
		},
		methods: {
			fn() {
				let data = {
						name: this.newcaipin.caipin,
						description: this.newcaipin.pinzhong,
						options: [{
							size: this.newcaipin.chicun,
							price: this.newcaipin.jiage
						}, {
							size: this.newcaipin.chicun2,
							price: this.newcaipin.jiage2
						}]
				}
				fetch('https://wd4709732241nqopkc.wilddogio.com/menu.json',{
					method:'POST',
					headers:{
						'Content-type':'application/json'
					},
					body:JSON.stringify(data)
				}).then(res => res.json())
				.then(data => {
					console.log(data) 
					alert('添加成功')
					this.$store.commit('pushmentItems',data)
					this.$router.push({name:'menuLink'})
				})
				
//				this.http.post('menu.json',data)
//				.then( data => {
//					console.log(data) 
//					alert('添加成功')
//					this.$store.commit('pushmentItems',data)
//				})
				
		}
	}
}	
</script>

<style>
	.d {
		margin: 15px 0;
	}
</style>


