<template>
	<div class="container bao_login">
		
		<div class="logo">
			<img src="../assets/logo.png"/>
		</div>
		<form @submit.prevent="login">			
			<div class="form-group">
				<label for="avcc">姓名：</label>
				<input type="text" class="form-control" id="avcc" placeholder="" v-model="name">
			</div>
			<div class="form-group">
				<label for="exampleInputEmail1">密码：</label>
				<input type="password" class="form-control" id="exampleInputEmail1" placeholder="" v-model="Mima">
			</div>
			<div class="form-group">
				<input type="submit" class="btn btn-success" style="width: 100%;" value="登录"></input>
			</div>
		</form>
		
	</div>
	
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				name:"",
				Mima:''
			}
		},
		beforeRouteEnter:(to,from,next)=>{
			next(vm => vm.$store.dispatch('setUser',null))
		},
		methods:{
			login(e){
				axios.get('/users.json')
				.then((res) => {
					console.log(res.data)
					const shuju = res.data
					const users = []
					for(let key in shuju){
						const user = shuju[key]
						console.log(user)
						users.push(user)
					}
					
					
					
					let result = users.filter((user)=>{
						console.log(user)
						return user.name === this.name && user.mima === this.Mima
					})
					
					console.log(result)
					
					if(result!=null && result.length>0){
						this.$store.dispatch('setUser',result[0].name)
						this.$message({
					          message: '登录成功^_^',
					          type: 'success'
					     });
						this.$router.push({name:'homeLink'})
					}else{
						this.$store.dispatch('setUser',null)
						this.$message.error('用户名或密码错误');
					}
					
					
				})
			}
		}
	}
</script>

<style>
.bao_login{
	width: 100%;
	height: auto;
	border: #28a745  solid 0px;margin: 10px 0;padding: 0 10px;margin: 0 auto;
}
.logo{
	margin: 20px 0;
}
.logo img{
	display: block;
	margin: 0 auto;
}
