<template>
	<div class="container">
		
		<!--<div class="row">
			<div class="background">
				<div class="bao">
					<p>品尝品尝来 </p>
					<button class="btn btn-info" @click="tiaohzuan">点击跳转</button>
				</div>
			</div>
			
		</div>-->
		
		<el-carousel indicator-position="outside" class='lunbotu'>
			    <el-carousel-item  v-for="admg in lunbotu"  :key="admg" >
			      <img :src="admg"/>
			    </el-carousel-item>
		</el-carousel>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				lunbotu:[
					'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1530608115291&di=cb82f64a4aa4db73596449d4aa4b2822&imgtype=0&src=http%3A%2F%2Fy3.ifengimg.com%2Fcmpp%2F2014%2F10%2F14%2F11%2F14384514-cc47-438b-a011-218af1a43da5.jpg',
					'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1530608115465&di=1cbab2278ba3534ca7a34f94dcec92a5&imgtype=0&src=http%3A%2F%2Fimg.mp.sohu.com%2Fupload%2F20170801%2F5870204eff47449abf6906ec6c3d28a6_th.png',
					'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1530608115465&di=a5d516561d8a6301fe73a00c62e496ba&imgtype=0&src=http%3A%2F%2Fimage5.tuku.cn%2Fpic%2Fwallpaper%2Fmeishi%2FXmasMeat%2F009.jpg',
					'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1530608115482&di=77b890557d1ff3aa35de418d21f0eb11&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fphotoblog%2F1406%2F20%2Fc2%2F35487604_1403249143274.jpg'
				]
			}
		},
		methods: {
			tiaohzuan() {

				//				this.$router.go(-1)    //返回跳到上一次的位置那里

				//				this.$router.replace('/admin')   //跳转到指定路由

				this.$router.replace({
					name: 'AboutLink'
				}) //通过路由name跳转到指定路由    push 和这个几乎擦不多

			}
		}
	}
</script>

<style>
.background{
	width: 100%;
	height: 70vh;margin-bottom: 20px;
	background: url(../assets/15.jpg) no-repeat;
	background-size: 100% 100%;
	position: relative;
}
.bao{
	width: 50%;
	height: auto;padding-bottom: 85px;
	position: absolute;
	left: 0;right: 0;bottom: 0;
	margin: auto;
	/*background: rgba(255,255,255,.65);
	border-radius: 10px;*/
}
.bao p{
	margin: 70px 0;
	text-align: center;
	color:#fff;
	font-size: 50px;display: none;
	font-weight: bold;border-radius: 10px;
	position: relative;
}
.bao button{
	
	display: block;border-radius: 10px;
	margin: 0 auto;
}
.lunbotu{
		height: ;
	}
.lunbotu img{
		width: 100%;
		height: 100%;
	}
	.el-carousel__arrow {
    border: none;
    outline: 0;
    padding: 0;
    margin: 0;
    height: 36px;
    width: 36px;
    cursor: pointer;
    -webkit-transition: .3s;
    transition: .3s;
    border-radius: 50%;
    background-color: rgba(31, 45, 61, 0.62);
    color: #fff;
    position: absolute;
    top: 50%;
    z-index: 10;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    text-align: center;
    font-size: 12px;
}
@media only screen and (max-width:900px) {
	.lunbotu{
		height: auto;
	}
	.lunbotu img{
		width: 100%;
		height: 100%;
	}
	
}
</style>


