<template>
	<div class="container bao_login">
		
		<div class="logo">
			<img src="../assets/logo.png"/>
		</div>
		<form @submit.prevent="login">			
			<div class="form-group">
				<label for="avcc">姓名：</label>
				<input type="text" class="form-control" id="avcc"  v-model="name">
			</div>
			<div class="form-group">
				<label for="Mima">密码：</label>
				<input type="password" class="form-control" id="Mima"  v-model="Mima">
			</div>
			<div class="form-group">
				<label for="ConfirmMima">密码：</label>
				<input type="password" class="form-control" id="ConfirmMima"  v-model="confirmMima">
			</div>
			<div class="form-group">
				<input type="submit" class="btn btn-success" style="width: 100%;" value="点击注册"></input>
			</div>
		</form>
		
	</div>
	
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				name:'',
				Mima:'',
				confirmMima:''
			}
		},
		methods:{
			login(e){
				if( this.name !='' && this.Mima !='' && this.confirmMima !='' && this.confirmMima == this.Mima){
					const data = {
						name:this.name,
						mima:this.Mima,
						confirmMima:this.confirmMima
					}
					axios.post('/users.json	',data)
					.then((res) => {
						 this.$message({
					          message: '注册成功^_^',
					          type: 'success'
					     });
						console.log(res)
						this.$router.push({name:"loginLink"})
					})
				}else if(this.name =='' || this.Mima =='' || this.confirmMima ==''){
					
					this.$message({
			          message: '信息没填完整真的好吗',
			          type: 'success'
			        });
			        
				}else{
					  this.$message.error('两次密码不一样，你让我很为难啊!')
				}
			}
		}
	}
</script>

<style>
	
.bao_login{
	width: 555px;
	height: auto;
	border: #28a745  solid 0px;margin: 10px 0;padding: 0 10px;width: 100%;margin: 0 auto;
}
.logo{
	margin: 0px 0;
}
.logo img{
	display: block;
	margin: 0 auto;
}
