import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


export const store = new Vuex.Store({
	state:{
		//设置属性
		menuItems:{},
		name:'',
		login:false
	},
	getters:{
//		获取属性的状态
		getmenuItems: state => state.menuItems,
		name: state => state.name,
		login: state => state.login
	},
	mutations:{
//		改变属性的状态
		setmentitems(state,data){
			
			state.menuItems = data
		},
		remove_menushuju(state,data){
			console.log(data)
			state.menuItems.forEach((item,index)=>{
				console.log(item,index)
				if(item == data){
					state.menuItems.splice(index,1)
				}
			})
		},
		//添加新的数据push到mentItems当中
		pushmentItems(state,data){
			state.menuItems.push(data)
		},
		//登录获取的name事件
		useStatus(state,user){
			console.log(user)
			if(user){
				state.name = user
				state.login = true
			}else{
				state.name = null
				state.login = false
			}
		}
	},
	actions:{
		//应用mutations
		setUser({commit},user){
			console.log(user)
			commit('useStatus',user)
		}
	}
})



