<template>
	<div class="row" style="padding: 0 10px;">
		<div class="col-sm-12 col-lg-7">
			<table class="table bj ">
				<thead >
					<tr class="active">
						<th>尺寸</th>
						<th>价格</th>
						<th>加入</th>
					</tr>
				</thead>
				<tbody v-for="(data,index) in getMenuItems" :key='data.name'>
					<tr style="" class="success">
						<!--<td style="width: 15%;padding: 5px 0;height: 70px;"><img :src="data.img" style="width: 100%;height: 100%;"/></td>-->
						<td><span style="font-size: 16px;background: #28a745;color: white;padding: 0 15px;margin-top: 5px;">{{data.name}}</span></td>
					</tr>
					<tr v-for="option in data.options">
						<td>{{option.size}} 寸</td>
						<td>{{option.price}}元</td>
						<td>
							<button class="btn btn-sm btn-outline-success" @click="addToBasket(data,option)">+</button>
						</td>
					</tr>

				</tbody>
			</table>
		</div>
		
		<div class="col-sm-12 col-lg-5" style="margin-top: 15px;margin-bottom: 35px;">
			<div v-show="newshuju.length>0?true:false">
				<table class="table" >
					<thead style="background: #28a745; color: white;">
						<tr>
							<th>数量</th>
							<th>品种</th>
							<th>尺寸</th>
							<th>价格</th>
						</tr>
					</thead>
					<tbody v-for="(item,index) in newshuju" >
						<tr>
							
							<td>
								<button class="btn btn-outline-success btn-sm" @click="jian(item,index)">-</button>
								<span style="padding: 0 10px;">{{item.sl}}</span>
								<button class="btn btn-outline-success btn-sm" @click="jia(item)">+</button>
							</td>
							<td>{{item.name}}</td>
							<td>{{item.size}} 寸</td>
							<td style="color: #28a745;font-weight: bold;">￥{{item.sl * item.price}}</td>
						</tr>				
					</tbody>	
				</table>
				<p style="color: red;font-weight: bold;">
					总价：{{total}} RMB
				</p>					
				<button class="btn btn-success btn-block">提交</button>	
				
			</div>
			<p v-show='newshuju.length<=0?true:false' style="line-height: 280px;text-align: center;color: #28a745;">没有数据（请先挑选你喜欢吃的哦）</p>
		</div>
		
		<!--{{newshuju}}-->
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				newshuju:[],
				
//				getMenuItems:[]
			}
		},
		created() {
//			fetch("https://wd4709732241nqopkc.wilddogio.com/menu.json")
//				.then(res => {
//					return res.json()
//				}).then(data => {
//					console.log(data)
//					this.getMenuItems = data
//				})

//				将请求下来的数据储蓄到vuex里面
				fetch("https://wd4709732241nqopkc.wilddogio.com/menu.json")
				.then(res => {
					return res.json()
				}).then(data => {
					console.log(data)
					this.$store.commit('setmentitems',data)
				})
		},
		methods:{
			addToBasket(data,option){
				console.log(data,option)
				let shuju = {
					name:data.name,
					size:option.size,
					price:option.price,
					sl:1
				}
				if(this.newshuju.length>0){
					let result = this.newshuju.filter((shuju)=>{
						return shuju.name == data.name && shuju.price == option.price
					})
					console.log(result)
					if(result!=null && result.length>0){
						result[0].sl++
					}else{
						this.newshuju.push(shuju)
					}
				}else{
					this.newshuju.push(shuju)
				}
				
			},
			jia(item){
				item.sl++
			},
			jian(item,index){
				item.sl--
				if(item.sl<=0){
					this.newshuju.splice(index,1)
				}
			}
		},
		computed:{
			total(){
				let totals = 0;
				for(let key in this.newshuju){
					let dxsj = this.newshuju[key]
					console.log(dxsj)
					totals += dxsj.sl * dxsj.price
				}
				return totals
			},
			getMenuItems(){
//				return this.$store.state.menuItems
//				通过getters来获取state的数据
				return this.$store.getters.getmenuItems
			}
		}
	}
</script>

<style>
.success{
	background: #fff;
}
.bj{
	/*filter:opacity(.4);*/
	/*background: url(../assets/timg.jpg) no-repeat;*/
}

</style>


