import Home from './components/home.vue'
import menu from './components/menu.vue'
import admin from './components/admin.vue'
import About from './components/About/About.vue'
import login from './components/login.vue'
import Register from './components/Register.vue'

//二级路由
import lishidingdan from './components/About/lishi.vue'
import lianxiwomen from './components/About/lianxiwomen.vue'
import diancan from './components/About/diancan.vue'
import kuaidi from './components/About/kuaidi.vue'

//三级路由
import tel from './components/About/tel-qq/tel.vue'
import qq from './components/About/tel-qq/qq.vue'

export const routes = [
	{
		path: "/home",
		name: "homeLink",
		components:{
			default:Home,
			'lishidingdan':lishidingdan,
			'diancan':diancan,
			'kuaidi':kuaidi
		}
	},
	{
		path: "/menu",
		name: "menuLink",
		component: menu
	},
	{
		path: "/admin",
		name: "adminLink",
		component: admin
//		路由独享守卫
//		beforeEnter:((to,from,next) => {
//			alert('没登录不能访问此页面')
//			next('/login')
//		})
	},
	{
		path: "/About",
		name: "AboutLink",
		component: About,
		redirect: '/About/lianxiwomen',
		children: [{
				path: "/About/lishidingdan",
				name: "lishidingdan",
				component: lishidingdan
			},
			{
				path: "/About/lianxiwomen",
				name: "lianxiwomen",
				component: lianxiwomen,
				children: [{
						path: "/tel",
						name: "telLlink",
						component: tel
					},
					{
						path: "/qq",
						name: "qqLlink",
						component: qq
					}
				]
			},
			{
				path: "/diancan",
				name: "diancan",
				component: diancan
			},
			{
				path: "/kuaidi",
				name: "kuaidi",
				component: kuaidi
			}
		]
	},
	{
		path: "/login",
		name: "loginLink",
		component: login
	},
	{
		path: "/Register",
		name: "RegisterLink",
		component: Register
	},
	{
		path: "*",
		redirect: '/home'
	}
]


